'use client';

import { useEffect, useState } from 'react';
import Loading, { ErrorDisplay, EmptyData } from './ui/loading';
import { RegionalPieChart, AlcoholReductionBarChart } from './OrdainChart';

// กำหนด type สำหรับข้อมูล
type RegionalData = {
  name: string;
  value: number;
};

type OrdainData = {
  projectName: string;
  implementationArea: { count: string; unit: string };
  policyExpansionArea: { count: string; unit: string };
  alcoholReduction?: { 
    total: string;
    templeEvents: string; 
    communityEvents: string 
  };
  modelMonks?: { count: string; unit: string };
  savings?: { 
    alcoholSavings: string; 
    expensePerMonk: string 
  };
  policy?: { count: string; unit: string };
  regionalData?: RegionalData[];
};

export default function OrdainStats() {
  const [data, setData] = useState<OrdainData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        const response = await fetch("/api/ordain");
        const result = await response.json();

        console.log("API Response:", result);

        if (response.ok && result.data) {
          setData(result.data);

          if (result.debug) {
            console.log("Debug Info:", result.debug);
          }
        } else {
          console.error("API Error:", result);
          setError(result.error || "เกิดข้อผิดพลาดในการดึงข้อมูล");
        }
      } catch (err) {
        console.error("Fetch Error:", err);
        setError("เกิดข้อผิดพลาดในการเชื่อมต่อกับเซิร์ฟเวอร์");
      } finally {
        setLoading(false);
      }
    }

    fetchData();
  }, []);

  // แสดงสถานะ Loading
  if (loading) {
    return <Loading size="large" />;
  }

  // แสดงข้อผิดพลาด
  if (error) {
    return (
      <ErrorDisplay
        message="ไม่สามารถโหลดข้อมูลได้"
        details={error}
        size="large"
      />
    );
  }

  // แสดงเมื่อไม่พบข้อมูล
  if (!data) {
    return (
      <EmptyData
        title="ไม่พบข้อมูล"
        message="ขออภัย ระบบไม่พบข้อมูลโครงการบวชสร้างสุข"
      />
    );
  }

  // ฟังก์ชันสำหรับแปลงตัวเลขให้เป็นรูปแบบที่อ่านง่าย
  const formatNumber = (num?: string) => {
    if (!num) return "0";
    return new Intl.NumberFormat("th-TH").format(parseInt(num));
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold text-center mb-8">
        {data.projectName}
      </h1>

      {/* แผนภูมิสรุปภาพรวม */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden mb-10">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-xl font-semibold">ภาพรวมโครงการบวชสร้างสุข</h3>
        </div>
        <div className="px-6 py-4">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-blue-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-blue-600">
                {formatNumber(data.implementationArea.count)}
              </p>
              <p className="text-sm text-blue-800 mt-2">
                พื้นที่ดำเนินการโครงการ
              </p>
            </div>
            <div className="bg-green-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-green-600">
                {data.modelMonks ? formatNumber(data.modelMonks.count) : "0"}
              </p>
              <p className="text-sm text-green-800 mt-2">
                พระต้นแบบที่ผ่านกระบวนการ
              </p>
            </div>
            <div className="bg-yellow-50 rounded-lg p-6 text-center">
              <p className="text-5xl font-bold text-yellow-600">
                {data.alcoholReduction
                  ? formatNumber(data.alcoholReduction.total)
                  : "0"}
              </p>
              <p className="text-sm text-yellow-800 mt-2">
                งานบวชที่ลดการบริโภคแอลกอฮอล์
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-10">
        {/* พื้นที่ดำเนินการ */}
        <div className="bg-gradient-to-br from-blue-50 to-blue-100 rounded-lg shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-blue-200">
            <h3 className="text-xl font-semibold text-blue-800">
              พื้นที่การดำเนินงาน
            </h3>
          </div>
          <div className="px-6 py-4">
            <div className="flex flex-col gap-6">
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-blue-700 mb-2">
                  พื้นที่ดำเนินการโครงการ
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.implementationArea.count)}
                  </span>
                  <span className="text-gray-600">
                    {data.implementationArea.unit}
                  </span>
                </div>
              </div>
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-blue-700 mb-2">
                  พื้นที่ขยายผลระดับนโยบาย
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.policyExpansionArea.count)}
                  </span>
                  <span className="text-gray-600">
                    {data.policyExpansionArea.unit}
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="px-6 py-4 bg-blue-50 text-sm text-blue-600 border-t border-blue-200">
            พื้นที่ปลอดภัย
            ที่มีการควบคุมการดื่มเครื่องดื่มแอลกอฮอล์ในงานบุญประเพณี
          </div>
        </div>

        {/* จำนวนพระต้นแบบ */}
        <div className="bg-gradient-to-br from-amber-50 to-amber-100 rounded-lg shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-amber-200">
            <h3 className="text-xl font-semibold text-amber-800">พระต้นแบบ</h3>
          </div>
          <div className="px-6 py-4">
            {data.modelMonks ? (
              <div className="bg-white rounded-lg p-6 shadow-inner mb-6">
                <h3 className="text-lg font-medium text-amber-700 mb-2">
                  จำนวนพระต้นแบบที่ผ่านกระบวนการบวชสร้างสุข
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.modelMonks.count)}
                  </span>
                  <span className="text-gray-600">
                    {data.modelMonks.unit || "รูป"}
                  </span>
                </div>
                <p className="mt-3 text-sm text-gray-600">
                  พระต้นแบบที่ยึดพระธรรมวินัย ประหยัด เรียบง่าย
                </p>
              </div>
            ) : (
              <EmptyData title="ไม่พบข้อมูลพระต้นแบบ" size="small" />
            )}

            {data.policy ? (
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-amber-700 mb-2">
                  นโยบาย/กติกา/สภาพแวดล้อม
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.policy.count)}
                  </span>
                  <span className="text-gray-600">
                    {data.policy.unit || "พื้นที่"}
                  </span>
                </div>
                <p className="mt-3 text-sm text-gray-600">
                  ที่เอื้อต่อการปรับเปลี่ยนค่านิยมการจัดงานบวช
                </p>
              </div>
            ) : (
              <EmptyData title="ไม่พบข้อมูลนโยบาย" size="small" />
            )}
          </div>
        </div>
      </div>

      {/* ข้อมูลการลดการบริโภคเครื่องดื่มแอลกอฮอล์ */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-10">
        <AlcoholReductionBarChart data={data.alcoholReduction} />

        {/* ข้อมูลสัดส่วนรายภูมิภาค */}
        {data.regionalData && <RegionalPieChart data={data.regionalData} />}
      </div>

      {/* ข้อมูลการประหยัด */}
      {data.savings ? (
        <div className="bg-gradient-to-br from-green-50 to-green-100 rounded-lg shadow-lg overflow-hidden mb-10">
          <div className="px-6 py-4 border-b border-green-200">
            <h3 className="text-xl font-semibold text-green-800">
              การประหยัดค่าใช้จ่าย
            </h3>
          </div>
          <div className="px-6 py-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-green-700 mb-2">
                  ประหยัดค่าเหล้าเบียร์
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.savings.alcoholSavings)}
                  </span>
                  <span className="text-gray-600">บาท</span>
                </div>
                <div className="mt-4 bg-green-100 p-3 rounded-lg">
                  <p className="text-sm text-green-800">
                    คิดเป็นเงินที่ประหยัดได้จากการลดการซื้อเครื่องดื่มแอลกอฮอล์ในงานบวช
                  </p>
                </div>
              </div>

              <div className="bg-white rounded-lg p-6 shadow-inner">
                <h3 className="text-lg font-medium text-green-700 mb-2">
                  ประหยัดค่าใช้จ่ายต่อการบวชพระ
                </h3>
                <div className="flex items-baseline">
                  <span className="text-4xl font-bold mr-2">
                    {formatNumber(data.savings.expensePerMonk)}
                  </span>
                  <span className="text-gray-600">บาท</span>
                </div>
                <div className="mt-4 bg-green-100 p-3 rounded-lg">
                  <p className="text-sm text-green-800">
                    ค่าใช้จ่ายที่ประหยัดได้จากการจัดงานบวชแบบเรียบง่าย
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="px-6 py-4 bg-green-50 text-sm text-green-600 text-center border-t border-green-200">
            สังคมไทยประหยัดค่าใช้จ่ายรวมกว่า{" "}
            {formatNumber(
              (
                parseInt(data.savings.alcoholSavings) +
                parseInt(data.savings.expensePerMonk)
              ).toString()
            )}{" "}
            บาท
          </div>
        </div>
      ) : (
        <EmptyData
          title="ไม่พบข้อมูลการประหยัดค่าใช้จ่าย"
          message="ไม่สามารถแสดงข้อมูลการประหยัดค่าใช้จ่าย"
        />
      )}

      {/* สรุปโครงการ */}
      <div className="bg-gradient-to-br from-indigo-50 to-indigo-100 rounded-lg shadow-lg overflow-hidden">
        <div className="px-6 py-4 border-b border-indigo-200">
          <h3 className="text-xl font-semibold text-indigo-800">
            สรุปผลสำเร็จของโครงการ
          </h3>
        </div>
        <div className="px-6 py-4">
          <div className="bg-white rounded-lg p-6 shadow-inner">
            <h3 className="text-lg font-medium text-indigo-700 mb-4">
              รูปธรรมความสำเร็จ
            </h3>
            <ul className="space-y-3">
              <li className="flex">
                <span className="mr-2 text-indigo-500">✓</span>
                <span>
                  พื้นที่ดำเนินการโครงการบวชสร้างสุข{" "}
                  <span className="font-semibold">
                    {formatNumber(data.implementationArea.count)}
                  </span>{" "}
                  พื้นที่
                </span>
              </li>
              <li className="flex">
                <span className="mr-2 text-indigo-500">✓</span>
                <span>
                  พื้นที่ขยายผลระดับนโยบาย{" "}
                  <span className="font-semibold">
                    {formatNumber(data.policyExpansionArea.count)}
                  </span>{" "}
                  พื้นที่
                </span>
              </li>
              {data.modelMonks && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>
                    พระต้นแบบที่ผ่านกระบวนการบวชสร้างสุข{" "}
                    <span className="font-semibold">
                      {formatNumber(data.modelMonks.count)}
                    </span>{" "}
                    รูป
                  </span>
                </li>
              )}
              {data.alcoholReduction && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>
                    ลดการบริโภคเครื่องดื่มแอลกอฮอล์ในงานบวช{" "}
                    <span className="font-semibold">
                      {formatNumber(data.alcoholReduction.total)}
                    </span>{" "}
                    งาน
                  </span>
                </li>
              )}
              {data.savings && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>
                    ประหยัดค่าใช้จ่ายรวม{" "}
                    <span className="font-semibold">
                      {formatNumber(
                        (
                          parseInt(data.savings.alcoholSavings) +
                          parseInt(data.savings.expensePerMonk)
                        ).toString()
                      )}
                    </span>{" "}
                    บาท
                  </span>
                </li>
              )}
              {data.policy && (
                <li className="flex">
                  <span className="mr-2 text-indigo-500">✓</span>
                  <span>
                    นโยบาย/กติกา/สภาพแวดล้อมที่เอื้อต่อการปรับเปลี่ยนค่านิยม{" "}
                    <span className="font-semibold">
                      {formatNumber(data.policy.count)}
                    </span>{" "}
                    พื้นที่
                  </span>
                </li>
              )}
            </ul>
          </div>
        </div>
        <div className="px-6 py-4 bg-indigo-50 text-sm text-indigo-600 text-center border-t border-indigo-200">
          งานบวชสร้างสุขต้นแบบ "ยึดพระธรรมวินัย ประหยัด เรียบง่าย" (ปลอดเหล้า
          ปลอดปัจจัยเสี่ยง ลดผลกระทบความรุนแรง)
        </div>
      </div>
    </div>
  );
}