'use client';

import React from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  Legend
} from 'recharts';
import { EmptyData } from './ui/loading';

type RegionalData = {
  name: string;
  value: number;
};

type ChartProps = {
  data: RegionalData[];
};

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8', '#83A6ED', '#8DD1E1', '#A4DE6C'];

export function RegionalPieChart({ data }: ChartProps) {
  if (!data || data.length === 0) {
    return <EmptyData title="ไม่พบข้อมูลภูมิภาค" message="ไม่สามารถแสดงข้อมูลสัดส่วนรายภูมิภาค" />;
  }

  return (
    <div className="bg-gradient-to-br from-teal-50 to-teal-100 rounded-lg shadow-lg overflow-hidden mb-10">
      <div className="px-6 py-4 border-b border-teal-200">
        <h3 className="text-xl font-semibold text-teal-800">สัดส่วนรายภูมิภาค</h3>
      </div>
      <div className="px-6 py-4">
        <div className="h-80">
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Pie
                data={data}
                cx="50%"
                cy="50%"
                labelLine={true}
                label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(1)}%`}
                outerRadius={120}
                fill="#8884d8"
                dataKey="value"
              >
                {data.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
              <Legend layout="vertical" verticalAlign="middle" align="right" />
              <Tooltip formatter={(value) => value} />
            </PieChart>
          </ResponsiveContainer>
        </div>
      </div>
      <div className="px-6 py-4 bg-teal-50 text-sm text-teal-600 text-center border-t border-teal-200">
        ข้อมูลการกระจายตัวของพื้นที่โครงการบวชสร้างสุขตามภูมิภาค
      </div>
    </div>
  );
}

type AlcoholReductionData = {
  total: string;
  templeEvents: string;
  communityEvents: string;
};

export function AlcoholReductionBarChart({ data }: { data?: AlcoholReductionData }) {
  if (!data) {
    return <EmptyData title="ไม่พบข้อมูลการลดการบริโภค" message="ไม่สามารถแสดงข้อมูลการลดการบริโภคเครื่องดื่มแอลกอฮอล์" />;
  }

  const chartData = [
    { name: 'งานบวชทั้งหมด', value: parseInt(data.total) || 0 },
    { name: 'งานบวชในวัด', value: parseInt(data.templeEvents) || 0 },
    { name: 'งานบวชในชุมชน', value: parseInt(data.communityEvents) || 0 },
  ];

  return (
    <div className="bg-gradient-to-br from-purple-50 to-purple-100 rounded-lg shadow-lg overflow-hidden">
      <div className="px-6 py-4 border-b border-purple-200">
        <h3 className="text-xl font-semibold text-purple-800">การลดการบริโภคเครื่องดื่มแอลกอฮอล์</h3>
      </div>
      <div className="px-6 py-4">
        <div className="h-72">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart data={chartData} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip formatter={(value) => new Intl.NumberFormat('th-TH').format(value as number)} />
              <Bar dataKey="value" fill="#8884d8">
                {chartData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
      <div className="px-6 py-4 bg-purple-50 flex justify-between border-t border-purple-200">
        <span className="text-sm text-purple-600">งานบวชทั้งหมด: {new Intl.NumberFormat('th-TH').format(parseInt(data.total) || 0)} งาน</span>
        <span className="text-sm text-purple-600">งานบวชในวัด: {new Intl.NumberFormat('th-TH').format(parseInt(data.templeEvents) || 0)} งาน</span>
      </div>
    </div>
  );
}